<template>
  <div class="health-items">
    <el-row v-for="(items,row) in healthList" :class="{'items-row':row!=healthList.length-1}">
      <el-col :span="12" v-for="(item,col) in items" :class="{'items-col':col==0}">
        <!-- <router-link :to="{name:'itemDetails',params:{id:item.id,title:item.name}}" style="text-decoration:none;"> -->
          <div class="cards iconfont" @click="itemDetails(item)">
              <span class="card-icon" :class="item.icon||'icon-yanjing'" :style="{background:item.icon_bgcolor||'#ff9600'}"></span>
              <p v-text="item.name"></p>
          </div>

      </el-col>
    </el-row>
    <!-- 添加新数据弹框 -->
    <el-dialog
      title="添加关注项目"
      v-model="dialogVisible"
      size="tiny"
      :close-on-click-modal="false"
      :show-close="false"
      @close="closeDialogCallback">
      <!-- <el-form :model="dataForm"  :rules="rules" ref="dataForm" label-position="top">
        <el-form-item prop="option"  label="名称">
          <el-select
            v-model="dataForm.option"

            clearable

            @change="defaultOption"
            placeholder="请选择">
            <el-option
              v-for="item in options"
              :label="item.name"
              :value="item" :disabled="item.name=='无数据'">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item prop="unit" label="单位">
            <el-input v-model="dataForm.unit" :maxlength='10' ></el-input>
        </el-form-item>
        <el-form-item label="正常参考值" >
          <el-col :span="11">
            <el-form-item prop="normal_min">
              <el-input :maxlength='10'  v-model="dataForm.normal_min" style="width: 100%;"></el-input>
            </el-form-item>
          </el-col>
          <el-col class="line" :span="2" style="text-align:center">～</el-col>
          <el-col :span="11">
            <el-form-item prop="normal_max">
              <el-input :maxlength='10' v-model="dataForm.normal_max" style="width: 100%;"></el-input>
            </el-form-item>
          </el-col>
        </el-form-item>
      </el-form> -->

      <form class="cards-form">
        <el-row>
          <el-col :span="24">
            <label for="name">名称</label>
            <select class="input"  v-model="dataForm.option" @change="defaultOption">
              <option value="" disabled="">请选择</option>
              <option :value="item" v-for="item in options">{{item.name}}</option>
            </select>
          </el-col>
        </el-row>
       <el-row>
        <el-col :span="24">
            <label for="">单位</label>
            <input class="input" type="text" v-model="dataForm.unit" :maxlength='10'>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <label for="">正常参考值</label>
            <el-row>
              <el-col :span="10">
                <input class="input" type="text" :maxlength='10' v-model="dataForm.normal_min">
              </el-col>
              <el-col :span="4" style="text-align:center;">
                ～
              </el-col>
              <el-col :span="10">
                <input class="input" type="text" :maxlength='10' v-model="dataForm.normal_max">
              </el-col>
          </el-col>
        </el-row>
      </form>

      <span slot="footer">
        <el-button @click="dialogVisible = false" style="border-color:#A21A28;">取 消</el-button>
        <el-button type="primary" @click="submit('dataForm')" class="tan_name">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 添加新数据弹框 end-->
  </div>

</template>

<script>
import { Indicator } from 'mint-ui';
export default {
  data (){
    var validateOption = (rule, value, callback) => {
        if (value == '') {
          callback(new Error('请输入关注项目名称'));
        } else {
          callback();
        }
      };
      var createErrorMessage = function(type){
        switch (type) {
          case "normal_min":
            return "请输入最小值";
            break;
          case "normal_max":
            return "请输入最大值";
            break;
          default:
            return "请输入";
        }
      }
      var validateNormalVal = (rule, value, callback) => {
          var errorMessage =createErrorMessage(rule.field);
          if (value == '') {
            callback(new Error(errorMessage));
          } else if(!Number(value)){
            callback(new Error('请输入数字'));
          }else{
            callback();
          }
        };
        var validateUnit = (rule, value, callback) => {
            if (value == '') {
              callback(new Error('请输入关注项目单位'));
            } else {
              callback();
            }
          };
    return {
        healthList:[],
        dialogVisible: false,
        options:[{name:"无数据"}],
        dataForm:{
            option:"",
            name:"",
            unit:"",
            normal_max:"",
            normal_min:"",
          },

        rules:{
          option: [
            { validator:validateOption, trigger: 'change' }
          ],
          unit: [
            { validator:validateUnit, trigger: 'change' }
          ],
          normal_max: [
            { validator:validateNormalVal, trigger: 'change' }
          ],
          normal_min: [
            { validator:validateNormalVal, trigger: 'change' }
          ]
        }
    }
  },

  methods:{
    defaultOption () {
      if(typeof(this.dataForm.option)=="string"){
        this.dataForm.name = this.dataForm.option || "";
      }else if(typeof(this.dataForm.option)=="object"){
        this.dataForm.name = this.dataForm.option.name || "";
        this.dataForm.unit = this.dataForm.option.unit || "";
        this.dataForm.normal_max = this.dataForm.option.normal_max || "";
        this.dataForm.normal_min = this.dataForm.option.normal_min || "";
      }

    },
    closeDialogCallback () {
      // this.$refs['dataForm'].resetFields();
      console.log("关闭了dialog");
      this.dataForm ={
            option:"",
            name:"",
            unit:"",
            normal_max:"",
            normal_min:"",
          };
    },
    getHealthitem (){
      // this.healthList=[];
      var resource = this.$resource(this.$store.state.serverIP +"/health_items/all_health_items?status=1&"+location.search.substring(1).split("&")[0]);
      resource.get().then((callback) => {
        try {
          if(callback.data.length>0){
            var list=[];
            for(var chr of callback.data){
              list.push(chr);
              if(list.length>=2){
                this.healthList.push(list);
                list = [];
              }

            }
            list.push({
              icon:"icon-plus",
              icon_bgcolor:"#ccc",
              type:"add-item"
            })
            this.healthList.push(list);

          }else{
            this.$toast('暂无数据');
          }
        }catch(error){
          this.$toast(error);
        }
        Indicator.close();
      },error=>{
        this.$toast(error);
        Indicator.close();
      });
    },
    submit(formName){//弹框的提交
        // this.dataForm.user_id = 2;
        //判断必填
        if (!this.dataForm.name) {
          this.$toast('请选择名称');
          return
        }
        if (!this.dataForm.unit) {
          this.$toast('请填写单位');
          return
        }

        if (!this.dataForm.normal_min || !this.dataForm.normal_min) {
          this.$toast('请填写参考值');
          return
        }

        if (isNaN(this.dataForm.normal_min) ||isNaN(this.dataForm.normal_max)) {
          this.$toast('正常参考值必须是数字');
          return
        }
        // this.$refs[formName].validate((valid) => {

        //         if (valid) {
        //           // alert('submit!');
                  var resource = this.$resource(this.$store.state.serverIP +"/health_items/add_health_item?"+location.search.substring(1).split("&")[0]);
                  resource.save(this.dataForm).then((response)=>{
                    this.dialogVisible = false;
                    if(this.healthList[this.healthList.length-1].length==1){
                      this.healthList[this.healthList.length-1].splice(0,1);
                      this.healthList[this.healthList.length-1].push(response.data.health_item);
                      this.healthList[this.healthList.length-1].push({
                        icon:"icon-plus",
                        icon_bgcolor:"#ccc",
                        type:"add-item"
                      })
                    }else{
                      this.healthList[this.healthList.length-1].splice(1,1);
                      this.healthList[this.healthList.length-1].push(response.data.health_item);
                       this.healthList.push([{
                        icon:"icon-plus",
                        icon_bgcolor:"#ccc",
                        type:"add-item"
                      }
                        ])
                    }

                    // this.$refs[formName].resetFields();

                  },(error)=>{

                    this.$toast({
                      position: 'center',
                      duration: 3000,
                      message:error.data.error
                    });
                  })
              //   } else {
              //     // console.log('error submit!!');
              //     // return false;
              //   }
              // });

      },

    itemDetails (item){
      if(item.type&&item.type=="add-item"){
        // this.$router.push({path:"/healthData"});
        this.dialogVisible = true;
        return false
      }
      try {
        if(item.subitems>0){
          var flag = true;
          for(var chr of item.subitems) {
            if(chr.sub_value_range==null){
              //去输入框
              this.$router.push({name:'itemInput',params:{id:item.id,title:item.name}});
              flag = false;
              break;
            }else{
              try {
                var list = chr.sub_value_range.split(",");
                if(list.length!=2){
                  this.$toast("数据错误，请输入选项值");
                  // 去输入框
                  this.$router.push({name:'itemInput',params:{id:item.id,title:item.name}});
                  flag = false;
                  break;
                }
              } catch (e) {
                  this.$toast("数据错误，请输入选项值");
                  // 去输入框
                  this.$router.push({name:'itemInput',params:{id:item.id,title:item.name}});
                  flag = false;
                  break;
              }
            }


          }
          if(flag){
            //去选择框
            this.$router.push({name:'itemSelect',params:{id:item.id,title:item.name}});
          }
        }else{
          if(item.value_range==null){
            //去输入框
            this.$router.push({name:'itemInput',params:{id:item.id,title:item.name}});
          }else{
            try {
              var list = item.value_range.split(",");
              if(list.length!=2){
                this.$toast("数据错误，请输入选项值");
                // 去输入框
                this.$router.push({name:'itemInput',params:{id:item.id,title:item.name}});

              }else{
                //去选择框
                this.$router.push({name:'itemSelect',params:{id:item.id,title:item.name}});
              }
            } catch (e) {
                this.$toast("数据错误，请输入选项值");
                // 去输入框
                this.$router.push({name:'itemInput',params:{id:item.id,title:item.name}});

            }
          }
        }
      } catch (e) {
        this.$toast(e);
      }
    }
  },
  mounted (){
    Indicator.open('加载中...');
    this.getHealthitem();
    var resource = this.$resource(this.$store.state.serverIP +"/health_items/get_custom_item?"+location.search.substring(1).split("&")[0]);
    resource.get().then(response=>{
      this.options = response.data.length>0?response.data:this.options;
    })
  }
}

</script>

<style  lang="scss">
.el-select{
  width: 100%;
}
.el-input{
  cursor:pointer
}
.health-items{

  .items-row{
    border-bottom:1px solid #e5e5e5;

  }
  .items-col{
    border-right:1px solid #e5e5e5;
  }
  .cards {
    span{
      display: block;
      margin: 20px auto;
      border-radius: 50%;
      height: 70px;
      line-height: 70px;
      text-align: center;
      color: #fff;
      font-size: 44px;
      width: 70px;
    }
    p{
      text-align: center;
      color: #323232;
    }
  }
}
.cards-form{
  box-sizing:border-box;
  label{
    line-height:30px;
    font-size:16px;
    display: block;
    font-family:'微软雅黑';
    margin:10px 0;
  }
  select.input{
    width:100%;
    padding:0px 2%;
    height:38px;
  }
  input.input{
    width:95%;
    padding:0px 2%;
  }
  .input{
    outline:none;
    height:35px;
    line-height:35px;
    border:1px solid #ccc;
    border-radius:4px;
    font-size:18px;
    display: block;
    background:transparent
  }
}

</style>



