<template>
	<div class="smark">
		<div class="warp_img" >
			<div class="spinner" v-if="isLoading">
			  <div class="spinner-container" :class="'container'+m" v-for="m in 3">
			    <div :class="'circle'+n" v-for="n in 4"></div>
			  </div>
			</div>
			<img class="picture" :src="url" id="warp_img" >
		</div>
		<slot></slot>
	</div>
</template >
<script >
import { Indicator } from 'mint-ui';
	export default {
		props:{
			url:String,
		},
		data () {
			return {isLoading:true}
		},
		methods:{
			 imgLoad(img, callback) {
	            var timer = setInterval(function() {
	                if (img.complete) {
	                    callback(img)
	                    clearInterval(timer)
	                }
	            }, 50)
        	},
        	move(imgId){
        		var resource = this.$resource(this.$store.state.serverIP +"/medical_record_managements/delete_medical_record_management?"+location.search.substring(1).split("&")[0]);
        		this.$confirm('您确定要删除吗?', '提示', {
		          confirmButtonText: '确定',
		          cancelButtonText: '取消',
		          type: 'warning'
		        }).then(()=>{
	        		resource.get({medical_record_management_id:imgId}).then((response)=>{
	        			this.close();
	        			location.reload();
	        			// console.log(response.data)
	        		},(error)=>{

	        		})
		        })
        	}
       
		},
		mounted (){
			var _this = this;
			this.imgLoad(document.getElementById('warp_img'),(img)=>{
				_this.isLoading = false;
			})
			
			
		}
	}
</script>
<style  lang="scss">
	.smark{
		top: 0px;
		left: 0px;
		width: 100%;
		height: 100%;
		position: fixed;
		background: rgba(0,0,0,.8);
		z-index: 99;
		.smark-button{
			width: 100%;
			height: 60px;
			text-align: center;
			span{
				display: inline-block;
				color:#fff;
				width: 60px;
				height: 60px;
				line-height: 60px;
				text-align: center;
				font-size:50px;
				
			}
		}
		.warp_img{
			width: 100%;
			height: calc(100% - 60px);
			position:relative;
			overflow: hidden;
			margin:0 auto;
		    text-align: center;
		    display: -webkit-box;
		    -webkit-box-align: center;
		    -webkit-box-pack: center;
		    display: -moz-box;
		    -moz-box-align: center;
		    -moz-box-pack: center;
		    display: -o-box;
		    -o-box-align: center;
		    -o-box-pack: center;
		    display: -ms-box;
		    -ms-box-align: center;
		    -ms-box-pack: center;
		    display: box;
		    box-align: center;
		    box-pack: center;
			img{
				border:2px solid #eee;
				display: block;
				max-width: 100%;
				max-height: 90%;
				height: auto;
				margin: 0 auto;
				overflow:hidden;
			}
				
		}
		
	}

	//加载动画  
	$boxSize:    40px !default;
	$blockSize:    10px !default;
	.spinner {
	 	margin: 100px auto;
	 	width: $boxSize;
	 	height: $boxSize;
	 	position: relative;
	 	.spinner-container {
		  position: absolute;
		  width: 100%;
		  height: 100%;
		}
	 	.container2 {
	  	-webkit-transform: rotateZ(45deg);
		  	transform: rotateZ(45deg);
		}
		.container3 {
		  	-webkit-transform: rotateZ(90deg);
		  	transform: rotateZ(90deg);
		}
		.circle1 { top: 0; left: 0; }
		.circle2 { top: 0; right: 0; }
		.circle3 { right: 0; bottom: 0; }
		.circle4 { left: 0; bottom: 0; }
		.container1> div, .container2 > div, .container3 > div {
		  	width: $blockSize;
		  	height: $blockSize;
		  	background-color: #fff;
		  	border-radius: 100%;
		  	position: absolute;
		  	-webkit-animation: bouncedelay 1.2s infinite ease-in-out;
		  	animation: bouncedelay 1.2s infinite ease-in-out;
		  	-webkit-animation-fill-mode: both;
		  	animation-fill-mode: both;
		}
		$num1:  2 !default;
		$num2:  1 !default;
		@for $i from 1 through 11 {
			
			@if $num1>3 {
				$num1:1;
				$num2:$num2 + 1;
			}
			@if $num2>4 {
				$num2:1
			}
			.container#{$num1} .circle#{$num2} {
			  -webkit-animation-delay: #{($i - 12) / 10}s;
			  animation-delay: #{($i - 12) / 10}s;
			}
			$num1: $num1 + 1;
		}
		@-webkit-keyframes bouncedelay {
		  0%, 80%, 100% { -webkit-transform: scale(0.0) }
		  40% { -webkit-transform: scale(1.0) }
		}
		 
		@keyframes bouncedelay {
		  0%, 80%, 100% {
		    transform: scale(0.0);
		    -webkit-transform: scale(0.0);
		  } 40% {
		    transform: scale(1.0);
		    -webkit-transform: scale(1.0);
		  }
	}
}
	



</style>


