<template>
	<div class="add-call">
		<!-- <mt-header title="家人" class="createName">
			<router-link to="/focusFamily" slot="left">
				<mt-button icon="back"></mt-button>
			</router-link>
		</mt-header> -->
		<div>
			<mt-field label="称呼" placeholder="请输入称呼" v-model="family.appellation" :attr="{ maxlength: 8 }" required></mt-field>
			<mt-field label="关注码" placeholder="请输入关注码" v-model="family.tel" :attr="{ maxlength: 11 }" required></mt-field>
		</div>

		<div class="next">
			<mt-button class="next-btn" @click.native="submitForm">确定</mt-button>
		</div>
	</div>
</template>
<script>
	export default {
		data (){
			return {
				family: {
					appellation: '',
					tel: ''
				}
			}
		},
		methods:{
			submitForm () {
				if (this.family.appellation==''){
					this.$toast({
						message: '请输入对方称呼',
						position: 'bottom'
					});
					return false
				}
				if(this.family.tel=='') {
					this.$toast({
						message: '请输入对方关注码',
						position: 'bottom'
					});
					return false

				}
				//提交被关注人接口
				var resource = this.$resource(this.$store.state.serverIP+'/user_focus/add_focu?'+location.search.substring(1).split("&")[0]);
				resource.save(this.family).then((response) => {
        			this.$toast({
        				message: '提交成功',
        				position: 'bottom'
        			});
        			setTimeout(() => {
        				this.$router.go(-1);
        			}, 1000);
				}, (response) => {
     				this.$toast(response.data.error);
				})
			}
		}
	}
</script>
<style scoped>
.add-call{
	padding: 30px 20px;
	font-family: '微软雅黑';
}
</style>



